import { useEffect, useState } from "react";
import { getSidebarCustomize } from "../sidebar";
import { getLuminosityRatio } from "../utils";
import "../style/controls/contrastRatio.scss";

const ContrastRatio = ({
  setting1,
  setting2,
  setting3,
  modifier,
  children
}) => {
  const customize = getSidebarCustomize();
  const [color1, setColor1] = useState(customize(setting1).get());
  const [color2, setColor2] = useState(customize(setting2).get());
  const [value3, setValue3] = useState(setting3 ? customize(setting3).get() : undefined);
  const [ratio, setRatio] = useState(0); // Listen to changes

  useEffect(() => {
    for (const {
      setting,
      setColor
    } of [{
      setting: setting1,
      setColor: setColor1
    }, {
      setting: setting2,
      setColor: setColor2
    }, setting3 ? {
      setting: setting3,
      setColor: setValue3
    } : undefined].filter(Boolean)) {
      customize(setting, value => {
        value.bind("change", newValue => {
          setColor(newValue);
        });
      });
    }
  }, [setting1, setting2, setting3]); // Calculate luminosity ratio

  useEffect(() => {
    const [useColor1, useColor2] = modifier ? modifier(color1, color2, value3, customize) : [color1, color2];
    setRatio(getLuminosityRatio(useColor1, useColor2));
  }, [color1, color2, value3, modifier]);
  return children(ratio);
};

export { ContrastRatio };